import React, { useEffect, useState } from "react";
import { Navbar, Nav, NavItem, Container, Row, Col } from "react-bootstrap";

// export default WebNav;
import logo from "../../img/logo.png";

function WebNav(props) {
    const [offset, setOffset] = useState(0)

    useEffect(() => {
        function handleScroll() {
            setOffset(window.pageYOffset)
        }
        window.addEventListener("scroll", handleScroll)
        return () => {
            window.removeEventListener("scroll", handleScroll)
        }
    }, [])

    
    return (
        <div className={(offset == 0 ) ? 'navbar-outer':'navbar-outer navMove'}>
            <div className='navbar-inner'>
                <div className='navbarBrand'>
                    <a href='/#' style={{ textDecoration: 'none' }}>
                        <div className='navbarBrand-inner'>
                            <img className='brandImg' src={logo} />
                            <div style={{ display: 'block', width: '1rem' }} />
                            <p className='brandName'>NCLab</p>
                        </div>
                    </a>
                </div>

                <Nav className="ml-auto mr-3">
                    <Nav.Link href="/#/contact">聯絡資訊</Nav.Link>
                    <Nav.Link href="/#/honors">獎項</Nav.Link>
                    <Nav.Link href="/#/people">實驗室成員</Nav.Link>
                    <Nav.Link href="/#/publications">發表</Nav.Link>
                    <Nav.Link href="/#/research">研究</Nav.Link>
                    <Nav.Link href="/#/photo">相片</Nav.Link>
                </Nav>
            </div>

        </div >
    )
}

export { WebNav }