import React, { Component } from "react";
import { Container, Row, Col, Button } from "react-bootstrap";
import { WebNav } from "../../components/navbar";
import { Footer } from '../../components/footer';
import { BsChevronDoubleDown, BsFillInfoSquareFill, BsChatSquareDotsFill } from "react-icons/bs";

/** image */
import logo from "../../img/logo.png";
import nckuLogo from "../../img/ncku_logo.png";
import nnImg from "../../img/nn.webp";
import bmiImg from "../../img/bmi.webp";


class Home extends Component {
  render() {
    return (
      <React.StrictMode>
        <WebNav />
        <div className='home'>
          <Cover />
          <Slogan />
          <Purpose />
          <About />
          <Future />
          <JoinUs />
        </div>
        <Footer />
      </React.StrictMode>
    );
  }
}

export { Home };


function Cover() {
  return (
    <div>
      <div className='cover'>
        <div className='cover-inner'>
          <div className='subtitle'>
            {/* <img src={nckuLogo} style={{height:'3rem'}} /> */}
            <p>NCKU</p>
          </div>
          <div className='title'>
            <p>Neural Computation<br />Laboratory</p>
          </div>
        </div>
        <div className='down animate__animated animate__bounce animate__infinite' style={{ color: '#403E3E' }}>
          <BsChevronDoubleDown />
        </div>
      </div>
    </div>

  )

}

function Slogan(params) {
  return (

    <div className='section-half' style={{ backgroundColor: '#5899E2' }}>
      <div className='subTitle my-5 ml-5'>
        <p>#</p>
      </div>
      <Container fluid className='h-100 w-100'>
        <Row className='h-100'>
          <Col className='h-100'>
            <div className='slogan ml-5 ml-md-0'>
              <div>
                <p>我們專門培養資料科學家</p>
                <p>We train students as data scientist</p>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  )
}

function Purpose(params) {
  return (

    <div className='section-half' style={{ backgroundColor: '#FFA400' }}>
      <div className='subTitle my-5 ml-5'>
        <p># purpose</p>
      </div>
      <Container fluid className='h-100 w-100'>
        <Row className='h-100'>
          <Col className='h-100'>
            <div className='slogan ml-5 ml-md-0'>
              <div style={{ fontSize: '2rem' }}>
                <p>本實驗室研究猴子與大鼠的神經訊號</p>
                <p>以類神經網路為基礎，設計侵入式腦機介面</p>
                <p>幫助患者以意念控制外部機器</p>
              </div>
            </div>
          </Col>
        </Row>
      </Container>

    </div>

  )
}

function About(params) {
  return (
    <div className='section' style={{ backgroundColor: '#403E3E' }}>
      <div className='subTitle my-5 ml-5' style={{ color: '#F0F0F0' }}>
        <p># Research</p>
      </div>
      <Container fluid className='about'>
        <Row>
          <Col>
            <div style={{ display: 'block', width: '100%', height: '10rem' }} />
          </Col>
        </Row>
        <Row>
          <Col xs={12} md={6}>
            <div className='card'>
              <div className='title'>
                <p>類神經網路<br />Neural Network</p>
              </div>
              <div className='imagebox'>
                <img className='h-100 w-100 p-5' src={nnImg} />
              </div>
            </div>
          </Col>
          <Col xs={12} md={6}>
            <div className='card'>
              <div className='title'>
                <p>侵入式腦機介面<br />Invasive Brain Machine Interface</p>
              </div>
              <div className='imagebox'>
                <img className='h-100 w-100 p-5' src={bmiImg} />
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  )
}

function Future(params) {
  return (
    <div className='section-half' style={{ backgroundColor: '#F0F0F0' }}>
      <div className='subTitle my-5 ml-5' style={{ color: '#403E3E' }}>
        <p># What you learn</p>
      </div>
      <Container fluid className='h-100 w-100'>
        <Row className='h-100'>
          <Col className='h-100'>
            <div className='slogan ml-5 ml-md-0'>
              <div style={{ fontSize: '1.5rem', letterSpacing: 'normal', color: '#403E3E' }}>
                <p>學生可學習到神經訊號處理，分析神經訊號的特性，獲得業界所需的資料分析與處理能力</p>
                <div style={{ display: 'block', height: '1.3rem' }} />
                <p>Students can learn neural signal process and understand neuronal characteristics.</p>
                <p>Students will possess data analysis and data processing skills which are needed in industry.</p>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  )
}

function JoinUs(params) {
  return (
    <div className='section' style={{ backgroundColor: '#5899E2' }}>
      <div className='subTitle my-5 ml-5' style={{ color: '#F0F0F0' }}>
        <p># Join Us</p>
      </div>
      <Container fluid className='h-100 w-100 joinUs'>
        <Row>
          <Col>
            <div style={{ display: 'block', width: '100%', height: '10rem' }} />
          </Col>
        </Row>
        <Row className=''>
          <Col xs={12} md={6} className=''>
            <div className='subSection' style={{ color: '#F0F0F0' }}>
              <BsFillInfoSquareFill style={{fontSize:'9rem'}} />
              <div style={{display:'block',height:'1.5rem'}} />
              <p>與實驗室聯繫</p>
              <Button className='' variant="outline-light" href='/#/contact'>
                <p className='m-0'>聯絡資訊</p>
              </Button>
            </div>
          </Col>
          <Col xs={12} md={6} className=''>
            <div className='subSection' style={{ color: '#F0F0F0' }}>
              <BsChatSquareDotsFill style={{fontSize:'9rem'}} />
              <div style={{display:'block',height:'1.5rem'}} />
              <p>碩士新生 面試報名</p>              
              <Button className='' variant="outline-light" target="_blank" href='https://docs.google.com/forms/d/e/1FAIpQLSddVTVVC7N0iLtpVYTZs3vcPknwJzIXBcXCjtT_mhQPcZY-tw/viewform'>
                <p className='m-0'>前往表單</p>
              </Button>
            </div>
          </Col>
        </Row>
      </Container>

    </div>
  )
}
